import React, { useContext, useEffect } from 'react';
import { MapContainer, TileLayer } from 'react-leaflet';
import AspectRatio from 'react-aspect-ratio';
import { LatLngTuple } from 'leaflet';
import { MapaContext, ThemeContext } from '../context';

const Mapa = ({ width, height, zIndex, opacity }: { width: any; height: any; zIndex: number; opacity: number }) => {
	const { setMapa, mapa } = useContext(MapaContext);
	const { theme } = useContext(ThemeContext);

	useEffect(
		() => {
			setTimeout(() => {
				!!mapa && mapa.invalidateSize();
			}, 100);
		},
		[ mapa ]
	);

	const position: LatLngTuple = [ 6.244747, -75.574828 ];
	const lightLayer = 'https://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}{r}.png';
	const darkLayer = 'https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png';
	const isLightLayer = theme === 'light';

	return (
		<AspectRatio
			ratio={width / height}
			style={{
				maxWidth: `${width}px`,
				zIndex: zIndex,
				opacity: opacity
			}}
			className="map-ratio"
		>
			<MapContainer
				attributionControl={false}
				center={position}
				doubleClickZoom={false}
				dragging={false}
				keyboard={false}
				scrollWheelZoom={false}
				tap={false}
				touchZoom={false}
				zoom={13}
				zoomControl={false}
				whenCreated={setMapa}
			>
				{isLightLayer && <TileLayer url={lightLayer} />}
				{!isLightLayer && <TileLayer url={darkLayer} />}
			</MapContainer>
		</AspectRatio>
	);
};

export default Mapa;
