import React, { createContext, useState, PropsWithChildren } from 'react';
import { Map } from 'leaflet';

const MapaContext = createContext<{
	mapa?: Map;
	setMapa?: (map: Map) => void;
}>({
	mapa: undefined,
	setMapa: undefined
});

function MapaProvider(props: PropsWithChildren<{}>) {
	const [ mapa, setMapa ] = useState<Map>();
	return (
		<MapaContext.Provider
			value={{
				mapa,
				setMapa
			}}
		>
			{props.children}
		</MapaContext.Provider>
	);
}

export { MapaContext, MapaProvider };
