import React, { useContext, useEffect, useLayoutEffect } from 'react';
import { MapaContext, ThemeContext } from '../context';
import { Icons } from '../components';
import { useWindowSize } from 'react-use';

export default function ThemeSelector(
	props: React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement>
) {
	const { theme, setTheme } = useContext(ThemeContext);
	const { mapa } = useContext(MapaContext);

	const { width, height } = useWindowSize();

	useEffect(
		() => {
			if (theme === 'light') {
				document.body.classList.add('light');
				document.body.classList.remove('dark');
			} else {
				document.body.classList.add('dark');
				document.body.classList.remove('light');
			}
		},
		[ theme ]
	);

	useLayoutEffect(
		() => {
			setTimeout(() => {
				!!mapa && mapa.invalidateSize();
			}, 100);
		},
		[ width, height, mapa ]
	);

	return (
		<button
			onClick={() => {
				setTheme(theme === 'light' ? 'dark' : 'light');
			}}
			{...props}
		>
			<span className="sr-only">Change web style to {theme === 'light' ? 'dark' : 'light'} theme</span>
			<Icons.Brightness width="24" className="fill-current" />
		</button>
	);
}
