import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import { Home } from './pages';
import { useLocalStorage } from 'react-use';
import { ThemeContext, MapaProvider } from './context';

const App = () => {
	const [ themeKey, themeKeySet ] = useLocalStorage('theme', 'dark');

	return (
		<ThemeContext.Provider
			value={{
				theme: themeKey,
				setTheme: themeKeySet
			}}
		>
			<MapaProvider>
				<Router>
					<Route component={Home} path="/" exact />
				</Router>
			</MapaProvider>
		</ThemeContext.Provider>
	);
};

export default App;
